"use client"

import type React from "react"
import { useState, useRef, useEffect } from "react"
import { Search, Send, Plus } from "lucide-react"

interface Message {
  role: "user" | "assistant"
  content: string
}

function FileBadge({ file, onRemove }: { file: File; onRemove: () => void }) {
  return (
    <button
      type="button"
      onClick={onRemove}
      className="p-2 rounded-full border border-blue-300 bg-blue-100 text-neutral-600 hover:bg-blue-200"
    >
      {file.name}
    </button>
  )
}

function MessageList({ messages, isLoading }: { messages: Message[]; isLoading: boolean }) {
  const messagesEndRef = useRef<HTMLDivElement>(null)

  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" })
  }, [messages])

  return (
    <div className="flex-1 overflow-y-auto mb-4 rounded-lg flex flex-col">
      {messages.map((message, index) => (
        <div key={index} className={`mb-4 ${message.role === "user" ? "text-right" : "text-left"}`}>
          <div className={`inline-block px-4 py-2 rounded-full ${message.role === "user" ? "bg-neutral-200 text-neutral-800 max-w-[80%]" : "text-neutral-800 max-w-[100%]"}`}>
            {message.content}
          </div>
        </div>
      ))}
      {isLoading && (
        <div className="text-left mb-4">
          <div className="inline-block px-4 py-2 rounded-lg text-neutral-800">
            <div className="flex space-x-1">
              <div className="w-2 h-2 bg-neutral-500 rounded-full animate-bounce"></div>
              <div className="w-2 h-2 bg-neutral-500 rounded-full animate-bounce" style={{ animationDelay: "0.2s" }}></div>
              <div className="w-2 h-2 bg-neutral-500 rounded-full animate-bounce" style={{ animationDelay: "0.4s" }}></div>
            </div>
          </div>
        </div>
      )}
      <div ref={messagesEndRef} />
    </div>
  )
}

function ChatInput({ input, setInput, isLoading, useWebSearch, setUseWebSearch, onSubmit }: any) {
  return (
    <form onSubmit={onSubmit} className="w-full max-w-4xl">
      <div className="bg-white rounded-3xl border border-neutral-300 px-2 py-2 shadow-lg mt-4">
        <input
          type="text"
          value={input}
          onChange={(e) => setInput(e.target.value)}
          placeholder="Type your message..."
          className="flex-1 p-2 font-medium bg-transparent focus:outline-none"
          disabled={isLoading}
        />
        <div className="flex items-center gap-2 justify-between w-full">
          <div className="flex items-center gap-2">
            <label className="flex items-center gap-1 p-2 border border-neutral-200 text-neutral-400 cursor-pointer rounded-full hover:bg-neutral-100 transition">
              <Plus size={20} />
              <input
                type="file"
                multiple
                hidden
                onChange={(e) => e.target.files}
              />
            </label>
            <button
              type="button"
              onClick={() => setUseWebSearch(!useWebSearch)}
              className={`flex items-center gap-1 p-2 rounded-full ${useWebSearch ? "border border-blue-300 bg-blue-100 text-neutral-600 hover:bg-blue-200" : "border border-neutral-200 text-neutral-400 hover:bg-neutral-100"}`}
            >
              <Search size={20} />
              <span className="text-sm font-semibold">Search</span>
            </button>
          </div>
          <button
            type="submit"
            disabled={isLoading || !input.trim()}
            className={`p-2 rounded-full ${!input.trim() || isLoading ? "border border-lime-300 bg-lime-200 text-white" : "border border-lime-500 bg-lime-400 text-white hover:bg-lime-500"}`}
          >
            <Send size={20} />
          </button>
        </div>
      </div>
    </form>
  )
}

export default function Chat() {
  const [messages, setMessages] = useState<Message[]>([])
  const [input, setInput] = useState("")
  const [isLoading, setIsLoading] = useState(false)
  const [useWebSearch, setUseWebSearch] = useState(false)

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    if (!input.trim()) return

    const userMessage: Message = { role: "user", content: input }
    setMessages((prev) => [...prev, userMessage])
    setInput("")
    setIsLoading(true)

    try {
      const response = await fetch("http://localhost:8000/query", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ query: userMessage.content, history: messages, use_web_search: useWebSearch })
      })
      if (!response.ok) throw new Error("Failed to get response")
      const data = await response.json()
      setMessages((prev) => [...prev, { role: "assistant", content: data.response || "Sorry, I couldn't process that request." }])
    } catch (err) {
      console.error("Error:", err)
      setMessages((prev) => [...prev, { role: "assistant", content: "Sorry, there was an error processing your request." }])
    } finally {
      setIsLoading(false)
    }
  }

  const isEmpty = messages.length === 0

  return (
    <div className="bg-neutral-100 font-sans min-h-screen flex items-center justify-center">
      <div className={`flex flex-col w-full max-w-3xl p-4 ${isEmpty ? "justify-center h-full" : "h-screen"}`}>
        {isEmpty && (
          <p className="font-bold text-black text-3xl text-center py-10">What can I help you with?</p>
        )}
        <MessageList messages={messages} isLoading={isLoading} />
        <ChatInput
          input={input}
          setInput={setInput}
          isLoading={isLoading}
          useWebSearch={useWebSearch}
          setUseWebSearch={setUseWebSearch}
          onSubmit={handleSubmit}
        />
      </div>
    </div>
  )
}